document.addEventListener("DOMContentLoaded", function(){
    initapp()
})

function initapp(){
    createGalery()
}

function createGalery(){
    const galeria = document.querySelector(".images-galery")
   for(let i = 1; i<=12; i++){
    const picture = document.createElement("picture")
    picture.innerHTML = `<source srcset="build/img/thumb/${i}.webp" type="image/webp" />
    <img
      laoding="lazy"
      src="build/img/thumb/${i}.jpg"
      alt="Imagen-galeria"
    />
    `
    galeria.appendChild(picture)
    picture.onclick = function(){
        showImage(i)
    }
   }
}

function showImage(id){
    //CREAR EL PICTURE
    const picture = document.createElement("picture")
    picture.innerHTML = `<source srcset="build/img/grande/${id}.webp" type="image/webp" />
    <img
      laoding="lazy"
      src="build/img/grande/${id}.jpg"
      alt="Imagen-galeria"
    />
    `
    const body = document.querySelector("body")
    //CREAR EL OVERLAY
    const overlay = document.createElement("DIV")
    overlay.appendChild(picture)

    //BOTON CERRAR

    const closeModal = document.createElement("Button")
    closeModal.textContent = "X"
    closeModal.classList.add("btn-close")
    closeModal.onclick = function(){
        overlay.remove()
        body.classList.remove("fix-body")
    }
    overlay.classList.add("overlay")
    overlay.appendChild(closeModal)
    overlay.onclick= function(){
        overlay.remove()
        body.classList.remove("fix-body")
    }
    body.appendChild(overlay)
    body.classList.add("fix-body")
}